class CompanyAddressService {

    static fillData(data) {

        console.log(data);
        $('input[name="city_id"').val(data.cidade);
        $('input[name="district_id"').val(data.bairro);
        $('input[name="place_id"').val(data.logradouro);

        $('.jq_address').removeAttr("disabled");

        let optionState = $('<option>')
            .text(data.estado)
            .attr("selected", true);

        $('select[name="state_id"').append(optionState);

        let optionCity = $('<option>')
            .text(data.cidade)
            .attr("selected", true);

        $('select[name="city_id"').append(optionCity);
   
    }

    static checkZipCode() {        

        $('input[name=zipcode_id]').on('keyup', function() {
            
            let number = $(this).val().replace(/[^0-9]/g, '').toString();

            if (number.length >= 8) {
             
                let parameters = {
                    data: { number: number} ,
                    url: '/admin/ajax/address/check/zipcode/'
                };

                let response = HttpService.ajax(parameters);

                response.done((data) => {                    
                    AddressService.fillData(data);
                });

                response.fail(() => {

                    //console.log(number.length);
                    //alert('Houve um erro na solicitação do pedido!');
                    //location.reload();
                });

            }

        });

    }

}